import React from 'react';
import './App.css';
import JokeCard from './files/JokeCard'
import Header from './files/Header'
import Footer from './files/Footer'

class App extends React.Component {

  state = {
    loading: true,
    joke: null,
  }

  async componentDidMount() {
    const url = "https://vast-hollows-83545.herokuapp.com/jokes";
    const response = await fetch(url);
    const data = await response.json();
    this.setState({joke: data});
    //console.log(this.state.joke)
  }
  
  render() {

    var jokeResult;
    

    
  return(
    
    <div>
      {!this.state.loading || !this.state.joke  ? <div>loading...</div> : <div>
          <Header/>
          {jokeResult = this.state.joke.map(i => <JokeCard key={i.id} question={i.question} awnser={i.answer}/>)}
          <Footer/>
        </div>}
      
  </div>
  )}
 
}

export default App;
